*{
  margin: 0;
  padding: 0;
  box-sizing:border-box;
}

body {
  margin: 0;
  padding: 0;
  background: radial-gradient(circle, rgb(5, 0, 36), rgba(5, 0, 36, 0.911) 70.71%) center center fixed;
  width: 100%;
  height: 100%;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.outlet {
  color: white;
}

/* STATEMENT CSS */

.statement {
  font-size: x-large;
  display: flex;
}


/* DIGITAL CSS */

.overlay {
  position: absolute;
  z-index: -1;
  top: 0;
  left: 0;
  height: 100vh;
  width: 100vw;
}

/* NAVBAR CSS */

.nav-bar {
  display: flex;
  position: sticky;
  align-items: center;
  align-content: center;
  justify-content: space-between;
  padding: .5rem clamp(1rem, 3.5vw, 5rem);
  background-color: rgba(255, 255, 255, .15);
  backdrop-filter: blur(5px);
}

.nav-bar * {
  display: inline;
  font-size: clamp(1rem, 3.5vw, 2rem);
  color: rgb(0, 174, 255);
  background-color: rgba(0, 0, 0, 0);
}

.nav-bar button {
  display: flex;
  align-items: center;
  gap: .5rem;
}

.nav-bar p {
  margin-bottom: 0;
}

.nav-wrap {
  display: flex;
  gap: clamp(1px, 2.5vw, 4rem);
}

.glitch {
  position: relative;
  top: 0;
  z-index: 1;
}

.glitch:before {
  content: attr(effect);
  position: absolute;
  left: -3px;
  top: 0;
  width: 100%;
  color: rgb(0, 255, 234);
  overflow: hidden;
  clip-path: rect(67px, 9999px, 122px, 0);
}

.glitch:after {
  content: attr(effect);
  position: absolute;
  left: 2px;
  top: 0;
  width: 100%;
  color: rgba(255, 255, 255, 0.507);
  overflow: hidden;
  clip-path: rect(63px, 9999px, 37px, 0);
}

/* expirements */

.buttons {
  display: flex;
  gap: 1rem;
  margin: 5rem;
  justify-content: center;
}

.expirement-wrapper {
  margin: auto;
}

.expirement {
  display: flex;
  height: 30vh;
  justify-content: center;
}

/* Resume css */
.page {
  color: black;
  text-align: start;
  margin: 1rem;
  padding: 1rem;
  background-color: rgba(240, 231, 182, 0.589);
  backdrop-filter: blur(8px);
}

.page h2 {
  text-align: center;
}

.page p {
  font-size: medium;
  font-weight: 400;
  color: rgba(0, 0, 0, 0.5);
  line-height: 1rem;
  margin: .5rem;
}

.page h4 p {
  display: inline;
  font-size: 1.5ch;
  font-weight: 400;
}

.info-date{
  display: grid;
  width: 100%;
  grid-template-columns: 3fr .5fr;
  grid-row: 1;
}

.info-date div{
  text-align: end;
}

.page li {
  max-width: 80ch;
}
